namespace Fable.Import

open System
open Fable.Core
open Fable.Import.Browser
open Fable.Import.JS
open collections.collections
open sharepoint.lists
open Fable.Core.JsInterop
open Fable.PowerPack

module HSharp2 =

    type [<AllowNullLiteral>] SimpleTypedHash<'T>() =
        interface TypedHash<'T> with
            [<Emit("$0[$1]{{=$2}}")>] member __.Item with get(key:string): 'T = jsNative and set(key:string) (v:'T): unit = jsNative

    [<Literal>]
    let Workbench = "https://localhost:4321/temp/workbench.html"

    let isWorkbench (w) = (w?location?href).ToString() = Workbench

    open sharepoint.queryable

    let selectQ ([<ParamArray>] selects: string[]) (q:QueryableInstance) : Queryable =
        q.select (selects) :> Queryable

    let get<'T> (q: Queryable) : Promise<'T> =
        q.get() |> Fable.PowerPack.Promise.map ( fun p -> p :?> 'T )

    let getListByTitle (lists:Lists) (title:string) : List =
        lists.getByTitle(title)
    
    let items (list:List) =
        list.items

    let value (fieldName:string) (o:obj) =
        o?(fieldName).ToString()

    let valueBool (fieldName:string) (o:obj) =
        ( o |> value fieldName ).ToLower() = true.ToString().ToLower()

    let valueAndConvert (fieldName:string) name convertor (o:obj) =
        let x = o |> value fieldName
        try            
            let res = x |> convertor
            let resS = sprintf "%A" res
            if resS = "null" then failwithf "%s failed to parse %A field %A with value %A" name o fieldName x
            res
        with
        | ex -> failwithf "%s failed to parse %A field %A with value %A" name o fieldName x

    let valueAndConvertO (fieldName:string) name convertor (o:obj) =
        let x = o |> value fieldName
        try  
            if x = "null" then None
            else          
                let res = x |> convertor
                let resS = sprintf "%A" res
                if resS = "null" then None else Some(res)
        with
        | ex -> failwithf "%s failed to parse %A field %A with value %A (optional)" name o fieldName x

    let valueDateTime (fieldName:string) (o:obj) =
        o |> valueAndConvert fieldName "valueDateTime" DateTime.Parse

    let valueInt (fieldName:string) (o:obj) =
        o |> valueAndConvert fieldName "valueInt" Int32.Parse

    let valueFloat (fieldName:string) (o:obj) =
        o |> valueAndConvert fieldName "valueFloat" parseFloat

    let valueDateTimeO (fieldName:string) (o:obj) =
        o |> valueAndConvertO fieldName "valueDateTime" DateTime.Parse

    let web url = sharepoint.webs.Web(U2.Case1(url))

    let getItemsFromList (web:sharepoint.webs.Web) (convert) (title:string) =
        title 
        |> getListByTitle (web.lists)
        |> items
        |> get<obj[]>
        |> Fable.PowerPack.Promise.map ( 
            fun o -> o |> Microsoft.FSharp.Collections.Array.map( convert )
        ) 


