module Blog

open HSharp
open Fable.Import
open Fable.Core

type Page() =
    interface IPage with
        member m.path = HSharp.PathAsterixSeparator + "#blog"
        member m.render () =
            let getPrispevek o = o
            let web = HSharp2.web ("https://naseukolycz-public.sharepoint.com")
            let prispevky = 
                "Prispevky" |> HSharp2.getItemsFromList web getPrispevek
            logO true "prispevky" prispevky
            ()



