module App

open HSharp
open Fable.Import
open Fable.PowerPack
open Fable.Core

type CoGoS2() =        
    interface IApplicationV2 with 
        member this.isDebug = false
        member this.getPages () = 
            [|
                Blog.Page()
            |]
        member this.getSites () =
            [|
            |]
        member this.getScheduledTasks() =
            [|
            |]

type Helpers()=
    let app = CoGoS2()
    let start () =
        HSharp.startApplication( ApplicationV2Wrapper(app) )



